






















import { Component, Vue } from "vue-property-decorator";
import SidebarMenu from "./components/SidebarMenu.vue";

@Component({
  components: {
    SidebarMenu
  }
})
export default class App extends Vue {
  isHidden = true;
  showMenu() {
    this.isHidden = false;
  }
  closeMenu() {
    this.isHidden = true;
  }
}
